.radio {
	position: relative;
	display: inline-block;
	vertical-align: top;
	min-width: 30px;
	min-height: 30px;
	@include hover {
		.radio {
			&__title {
				color: $blue; } } }
	@include hover('metro') {
		.radio {
			&__title {
				color: $metroSecondary; } } }
	input {
		display: none;
		&:checked {
			~ .radio {
				&__icon {
					border-color: $blue;
					@include metro {
						border-color: $metroSecondary; }
					&:before {
						opacity: 1;
						background: $blue;
						@include metro {
							background: $metroSecondary; } } } } }
		&:disabled {
			~ .radio {
				&__icon,
				&__title {
					opacity: 0.5;
					cursor: not-allowed; } } } }
	&__icon {
		position: absolute;
		top: 0;
		left: 0;
		border: 1px solid $darkGray;
		border-radius: 50%;
		width: 30px;
		height: 30px;
		cursor: pointer;
		transition: border-color 0.25s;
		&:before {
			content: '';
			position: absolute;
			top: 5px;
			left: 5px;
			right: 5px;
			bottom: 5px;
			border-radius: 50%;
			opacity: 0;
			background: $darkGray;
			transition: opacity 0.25s, background 0.25s; } }
	&__text {
		display: inline-block;
		vertical-align: top;
		padding: 5px 0 5px 40px;
		color: inherit;
		cursor: pointer;
		transition: color 0.25s; } }
